@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+NKo+Unjoined:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playball&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,100..900;1,100..900&display=swap');

@font-face {
  font-family: CascadiaCode;
  src: url('fonts/CascadiaCode.woff2');
}
@font-face{
  font-family: Neon;
  src:url('fonts/neon.ttf')
}
@layer components{
  .btn{
   @apply bg-red-800 hover:bg-red-700 text-white font-bold py-2 px-4 rounded hover:text-neutral-200
  }
}

@layer utilities{
  .border-bottom{
    background: linear-gradient(
            rgb(255, 255, 255),
            rgb(255, 255, 255));
    background-size: 100% 2px;
    background-position: 100% 100%, 0 100%;
    background-repeat: no-repeat;
  }
  .neon{
    font-family: Neon;
    text-shadow:
    0 0 6px rgba(255, 255, 255, 0.92),
    0 0 4px rgba(250, 252, 255, 0.52),
  }
}